import Hero from './Hero';
import About from './About';
import Courses from './Courses';
import WhyUs from './WhyUs';
import Cta from './Cta';
import Footer from './Footer';

import useStore from './stateStore';
import { useTransition, animated } from 'react-spring'

function App() {

    const loadedModels = useStore(state => state.loadedModels);
    const models = useStore(state => state.models);

    const transitions = useTransition(loadedModels === models, {
        from: { opacity: 0 },
        enter: { opacity: 1 },
        leave: { opacity: 0 },
        delay: 1000
    });

    return transitions((styles, loaded) => {
        return loaded ? (
            <animated.div style={styles} className="app">
                <Hero />
                <About />
                <Courses />
                <WhyUs />
                <Cta />
                <Footer />
            </animated.div>
        )
        :
        (
            <animated.h1 style={{...styles, zIndex: '10'}} className="fixed-full bg-white">Cargando...</animated.h1>
        )
    })


}

export default App;
