import { Canvas, useFrame, useLoader, useThree } from "@react-three/fiber";
import { Suspense, useCallback, useEffect, useLayoutEffect, useMemo, useRef } from "react";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { MathUtils, Matrix4, Object3D } from "three";

import useStore from '../stateStore';

window.BONSAI = {};
window.BONSAI.mouse = {
    x: 0,
    y: 0
}

function getRandomInt(min, max) {
    return Math.random() * (max - min) + min;
}

function Bonsai() {
    const object = useLoader(GLTFLoader, 'models/bonsai.glb');
    const addLoadedModel = useStore(state => state.addLoadedModel);
    useEffect(() => {
        addLoadedModel();
    }, [])

    object.scene.position.y = -5;
    object.scene.position.z = -10;

    return <primitive object={object.scene} />
}

function Leaf() {

    const instanceRef = useRef(null);

    const object = useLoader(GLTFLoader, 'models/leaf.glb');
    const addLoadedModel = useStore(state => state.addLoadedModel);

    useEffect(() => {
        addLoadedModel();
    }, [])

    const tempObject = useMemo(() => new Object3D(), []);
    const tempMatrix = useMemo(() => new Matrix4(), []);

    const size = useThree((state) => state.size);
    const set = useThree((state) => state.set);

    const createLeaves = useCallback((range, position, limits) => {

        for (let i = range.from; i < range.to; i++) {

            // position X
            const x = position.x;
            const randX = Math.random()
            const distX = (limits.x / 2) - (randX * limits.x);
            tempObject.position.x = distX + x;

            // position Y
            const y = position.y;
            const curve = 1.0 - (Math.pow(randX - 0.5, 2) * 10);

            const randY = Math.random()
            const distY = (limits.y / 2) - (randY * limits.y) + (curve);
            tempObject.position.y = distY + y;

            // position Z
            const z = position.z;
            const randZ = Math.random()
            const distZ = (limits.z / 2) - (randZ * limits.z);
            tempObject.position.z = distZ + z;


            tempObject.rotation.x = 2 - (Math.random() * 4);
            tempObject.rotation.y = 2 - (Math.random() * 4);
            tempObject.rotation.z = 2 - (Math.random() * 4);
            tempObject.scale.setScalar(getRandomInt(0.6, 1));

            tempObject.updateMatrix();
            instanceRef.current.setMatrixAt(i, tempObject.matrix);
            instanceRef.current.instanceMatrix.needsUpdate = true;
        }

    }, []);

    const moveLeavesWithMouse = useCallback((ev) => {
        window.BONSAI.mouse.x = 0.5 - (ev.clientX / size.width);
        window.BONSAI.mouse.y = 0.5 - (ev.clientY / size.height);
    }, [size.width, size.height]);

    useLayoutEffect(() => {
        instanceRef.current.position.set(-1, 3, -10.4);

        instanceRef.current.setMatrixAt(0, tempMatrix);
        instanceRef.current.scale.setScalar(0.14);

        createLeaves(
            { from: 0, to: 400 },
            { x: 0, y: -3, z: 0 },
            { x: 20, y: 20, z: 20 }
        );

        createLeaves(
            { from: 400, to: 800 },
            { x: 10, y: 10, z: 0 },
            { x: 20, y: 10, z: 20 }
        );

        createLeaves(
            { from: 800, to: 1500 },
            { x: 23, y: 0, z: 0 },
            { x: 30, y: 20, z: 20 }
        );

        createLeaves(
            { from: 1500, to: 2100 },
            { x: 25, y: -20, z: 0 },
            { x: 40, y: 20, z: 10 }
        );

        createLeaves(
            { from: 2100, to: 2500 },
            { x: 20, y: -40, z: 0 },
            { x: 30, y: 10, z: 20 }
        );

        createLeaves(
            { from: 2500, to: 3400 },
            { x: -20, y: -40, z: 0 },
            { x: 40, y: 20, z: 20 }
        );

        createLeaves(
            { from: 3400, to: 4000 },
            { x: -8, y: -20, z: 0 },
            { x: 30, y: 12, z: 20 }
        );

        createLeaves(
            { from: 4000, to: 4200 },
            { x: 9, y: -20, z: 0 },
            { x: 140, y: 50, z: 80 }
        );
        const animatedLeaves = [];
        for (let i = 4000; i < 4200; i++) {
            const leaf = new Object3D();

            instanceRef.current.getMatrixAt(i, tempMatrix);
            leaf.position.setFromMatrixPosition(tempMatrix);
            leaf.rotation.setFromRotationMatrix(tempMatrix);
            leaf.updateMatrix();

            animatedLeaves.push(leaf);
        }
        set({
            animatedLeaves: animatedLeaves
        });

        window.addEventListener('mousemove', moveLeavesWithMouse);

        return () => {
            window.removeEventListener('mousemove', moveLeavesWithMouse);
        }

    }, [moveLeavesWithMouse, set ]);

    useFrame((state) => {

        if (state.animatedLeaves.length <= 0) {
            return
        }

        let j = 0;
        const scrollWhyUs = useStore.getState().scrollWhyUs;
        for (let i = 4000; i < 4200; i++) {

            instanceRef.current.getMatrixAt(i, tempMatrix);
            tempObject.position.setFromMatrixPosition(tempMatrix);
            tempObject.rotation.setFromRotationMatrix(tempMatrix);

            tempObject.position.x = MathUtils.lerp(tempObject.position.x, state.animatedLeaves[j].position.x + window.BONSAI.mouse.x * 50, 0.01);
            tempObject.position.y = state.animatedLeaves[j].position.y + (Math.sin(state.clock.getElapsedTime() + state.animatedLeaves[j].position.y)) * 2;
            tempObject.position.z = MathUtils.lerp(tempObject.position.z, state.animatedLeaves[j].position.z * 15 * scrollWhyUs, 0.01 );
            j++;

            tempObject.rotation.z = Math.PI * state.clock.getElapsedTime() * 0.5;


            tempObject.updateMatrix();

            instanceRef.current.setMatrixAt(i, tempObject.matrix);
            instanceRef.current.instanceMatrix.needsUpdate = true;
        }
        state.scene.rotation.y = MathUtils.lerp(state.scene.rotation.y, window.BONSAI.mouse.x * 0.25, 0.01);
        state.camera.position.z = MathUtils.lerp(state.camera.position.z, 2 + ((useStore.getState().scrollHero) ) * 20, 0.01) ;
    });


    return (
        <instancedMesh
            ref={instanceRef}
            args={[object.scene.children[0].geometry, object.scene.children[0].material, 4200 + 1]}
        />
    )

}

function MyCanvas() {

    return (
        <Canvas raycaster={null} dpr={Math.min(window.devicePixelRatio, 2)}>
            <ambientLight color="white" intensity={.3} />
            <Suspense fallback={null}>
                <Bonsai />
                <Leaf />
            </Suspense>
        </Canvas>
    )
}

export default MyCanvas;