import ScrollTrigger from 'react-scroll-trigger';
import useStore from './stateStore';

function WhyUs() {

    const body = document.querySelector('body');
    function enableBackgroundColor(e){
        body.style.backgroundColor = '#191F18';
    }
    function disableBackgroundColor(e){
        if(e.progress === 1){
            return;
        }
        body.style.backgroundColor = '#FCFAFE';
    }

    function onScrollProgress(e) {
        useStore.setState({
            scrollWhyUs: e.progress
        });
    }

    return (
        <ScrollTrigger onProgress={onScrollProgress} onExit={disableBackgroundColor} onEnter={enableBackgroundColor}>
            <section className="white p-l2 relative">

                <article className="flex">

                    <div className="offset columns-1">
                    </div>

                    <div className="columns-4">
                        <h2 className="mb-l2">¿POR QUÉ ELEGIRNOS?</h2>

                        <div className="columns-3 text-justify">
                            <p className="mb-l2">
                                Nos dedicamos a profundizar en la artesanía y el arte del bonsái. Usando la colección Bonsai West y los árboles de los estudiantes, practicaremos la horticultura y la teoría del diseño, cultivando una comprensión profunda del bonsái en muchas especies.
                            </p>
                            <p>
                                El plan de estudios de la escuela está diseñado para ser una clase magistral de un año. Se hace hincapié en obtener una comprensión de todos los principios de la excelencia creciente. Durante el primer año, los estudiantes completarán la transformación de un ejemplar de Shimpaku Juniper en un bonsái fino y único.
                            </p>
                        </div>

                    </div>

                </article>

                <div className="h-25vh flex items-end columns-1 m-0 md-m-l2 absolute right-0 bottom-0">
                    <video autoPlay loop muted>
                        <source src="images/video.mp4" />
                    </video>
                </div>

            </section>
        </ScrollTrigger>
    );
}

export default WhyUs;
