import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import App from './App';
import Canvas from './canvas';

function Portfolio() {
    return (
        <>
            <App />
            <div style={{zIndex: -2}} className="h-100vh fixed-full">
                <Canvas />
            </div>
        </>
    );
}

ReactDOM.render(
    <React.StrictMode>
        <Portfolio />
    </React.StrictMode>,
    document.getElementById('root')
);
