import { useSpring, animated } from 'react-spring'
import ScrollTrigger from 'react-scroll-trigger';

function Course(props) {

    const [buttonProps, buttonSpring] = useSpring(() => ({
        from: {
            x: 0,
            y: 0,
        }
    }));

    const [imageProps, imageSpring] = useSpring(() => ({
        from: {
            x: 0,
            y: 0,
            height: '0%'
        }
    }));

   function buttonMouseMovement(e) {
       const rect = e.target.getBoundingClientRect();
       const x = 0.5 - (e.clientX - rect.left) / rect.width;
       const y = 0.5 - (e.clientY - rect.top) / rect.height;

       buttonSpring.start({
           x: 1- (x * 100),
           y: 1- (y * 100)
       })
   }

   function imageMouseMovement(e) {
       const rect = e.target.getBoundingClientRect();
       const x = (e.clientX - rect.left);
       const y = (e.clientY - rect.top);

       imageSpring.start({
           x: x,
           y: y
       });
   }

   function revealImage() {
       imageSpring.start({
           height: '150%',
       });
   }

   function hideImage() {
       imageSpring.start({
           height: '0%',
       });
   }

   return (
       <div style={{borderBottom: 'solid 1px #b7c7ba'}} className="px-l2 pb-l2 flex flex-wrap items-center mb-l6">
           <div className="columns-5 relative" onMouseLeave={hideImage} onMouseEnter={revealImage} onMouseMove={imageMouseMovement}>
               <h3>{props.title}</h3>
               <p className="columns-4">
                   {props.desc}
               </p>
               <animated.img style={{...imageProps, objectFit: 'none'}} className="z--1 absolute w-30pc top-0" src={props.image} />
           </div>

           <div className="columns-1">
               <button onMouseMove={buttonMouseMovement} className="block m-auto">
                   <span className="label">
                       INSCRIBIRME
                   </span>
                   <animated.span style={buttonProps} className="dot" />
               </button>
           </div>
       </div>
   )
}

function Courses() {

    const [imageProps, imageSpring] = useSpring(() => ({
        from: {
            borderWidth: 0
        }
    }));

    const [textProps, textSpring] = useSpring(() => ({
        from: {
            x: '0%'
        }
    }));


    function onScrollProgress(e) {
        imageSpring.start({borderWidth: (e.progress * 100)});

        const n = -200 * e.progress;
        textSpring.start({x: `${n}%`});
   }
    return (
        <section className="mb-l10">
            <article className="relative overflow-hidden mb-l10">
                <ScrollTrigger onProgress={onScrollProgress}>
                    <animated.img style={{...imageProps, objectFit: 'none', borderStyle: 'solid', borderColor: '#FCFAFE'}} src="images/bonsai2.webp" alt="bonsai 2" className="w-100pc h-100vh"  />
                </ScrollTrigger>

                <animated.h2 style={{...textProps, whiteSpace: 'nowrap'}} className="text-center absolute-center w-100pc white">
                    CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS CURSOS 
                </animated.h2>
            </article>

            <article>

                <section>
                    <Course 
                        title={'Bonsái de árbol de limón'}
                        desc={'Nigel Saunders entrenando sobre cómo podar un limonero para convertirlo en bonsái, incluida la defoliación, la poda de raíces y el trasplante.'}
                        image={'images/courses/1.webp'}
                    />

                    <Course 
                        title={'Bonsái Mirai'}
                        desc={'Este documental traza el camino de Ryan Neil mientras construye un movimiento American Bonsai Movement'}
                        image={'images/courses/2.webp'}
                    />

                    <Course 
                        title={'Poda y fertilización de bonsáis de pino'}
                        desc={'Ryan Neil presenta sobre poda en Willowbog, Hexham UK'}
                        image={'images/courses/3.webp'}
                    />

                </section>

            </article>
        </section>
    );
}

export default Courses;
