import { useSpring, animated } from 'react-spring'
import ScrollTrigger from 'react-scroll-trigger';
import useStore from './stateStore';

function Hero() {

    const heroProps = useSpring({
        from: {
            y: '100%',
        },
        to: {
            y: '0%',
        },
        config: { tension: 120 },
        delay: 1000,
    });

    function onScrollProgress(e) {
        useStore.setState({
            scrollHero: (e.progress / 0.5) -1
        });
    }

    return (
        <>
            <ScrollTrigger onProgress={onScrollProgress}>

                <section className="p-l2 h-100vh relative mb-l10">
                    <article className="overflow-hidden">
                        <animated.h1 style={heroProps}>
                            <span className="text-stroke-1 text-stroke-black transparent">EL </span>
                            ARTE
                            <span className="text-stroke-1 text-stroke-black transparent"> DE TENER </span>
                            PACIENCIA
                        </animated.h1>
                    </article>

                    <div className="z--1 absolute flex flex-wrap columns-6 left-0 px-l2" style={{ top: '33%' }}>
                        <div className="h-5 columns-1">
                        </div>

                        <div className="h-50vh bg-gray columns-4 opacity-10 z--1">
                        </div>
                    </div>

                    <article className="hidden md-flex flex-wrap items-end" style={{ height: '60%' }}>
                        <div className="columns-1">
                        </div>
                        <div className="columns-2">
                            <strong>Técnicas e inspiración</strong>
                            <p>de los mejores profesores de bonsái</p>
                        </div>
                        <div className="columns-2">
                            <strong>Profesores en todo el mundo</strong>
                            <p>para principiantes y avanzados</p>
                        </div>

                        <div className="columns-1 text-right">
                            <span className="big arrow">↓</span>
                        </div>
                    </article>
                </section>
            </ScrollTrigger>
        </>
    );
}

export default Hero;
