import { useSpring, animated } from 'react-spring'
import ScrollTrigger from 'react-scroll-trigger';

function Cta() {

    const [imageProps1, imageSpring1] = useSpring(() => ({
        from: {
            y: '0%',
        }
    }));

    const [imageProps2, imageSpring2] = useSpring(() => ({
        from: {
            y: '0%',
        }
    }));

    const [imageProps3, imageSpring3] = useSpring(() => ({
        from: {
            y: '0%',
        }
    }));

    function onScrollProgress(e) {
        let n = -e.progress * 200;
        imageSpring1.start({
            y: `${n}%`
        });

        n = -e.progress * 10;
        imageSpring2.start({
            y: `${n}%`
        });

        n = -e.progress * 200;
        imageSpring3.start({
            y: `${n}%`
        });
    }

    return (
        <>
            <ScrollTrigger onProgress={onScrollProgress}>
                <section style={{ height: '300vh' }} className="bg-dark white px-l2 relative">

                    <animated.img style={{ ...imageProps1, left: '1em', top: '40%' }} className="absolute w-30pc" src="images/bonsai3.webp" />
                    <animated.img style={{ ...imageProps2, right: '1em', bottom: '30%' }} className="absolute w-30pc" src="images/bonsai4.webp" />
                    <animated.img style={{ ...imageProps3, right: '33%', bottom: '0%' }} className="absolute w-30pc z-10" src="images/bonsai5.webp" />
                    <h2 style={{ position: 'sticky', top: 0 }} className="text-center h-100vh flex items-center">¿YA QUIERES SER UN ARTISTA DE LA PACIENCIA?</h2>
                </section>
            </ScrollTrigger>
        </>
    );
}

export default Cta;
