import { useSpring, animated } from 'react-spring'
import ScrollTrigger from 'react-scroll-trigger';

function About() {

    const [imageProps, api] = useSpring(() => ({
        from: {
            y: '0%'
        },
    }));

    function onScrollProgress(e) {
        const n = -e.progress * 30;
        api.start({
            y: `${n}%`
        });
    }

    return (
        <section className="pl-2 mb-l10">
            <article className="w-100pc flex relative">

                <div className="offset columns-1">
                </div>

                    <ScrollTrigger onProgress={onScrollProgress}>
                        <animated.img style={imageProps} src="images/bonsai1.webp" alt="bonsai 1" className="z--1 absolute right-0  top-0 columns-4 px-l2" />
                    </ScrollTrigger>

                <div className="px-l2 z-0">

                    <h2 className="columns-4">
                        <span className="text-stroke-1 text-stroke-black transparent">Conviértete en</span>
                        <br />
                        Master
                        <span className="text-stroke-1 text-stroke-black transparent"> del </span>
                        Bonsai
                        <br />
                        <span className="text-stroke-1 text-stroke-black transparent"> a tu </span>
                        propio
                        <br />
                        ritmo
                    </h2>

                    <div className="columns-2 text-justify">
                        <p className="mb-4">
                            Nuestra misión es hacer crecer el número total de artistas del bonsái en todo el mundo. Bonsaicourses.com es un esfuerzo por recopilar y centralizar el mejor material educativo sobre bonsai en Internet.
                        </p>
                        <p>
                            El arte del bonsái ha existido durante miles de años, pero todos los días, los maestros de todo el mundo comparten sus aprendizajes y técnicas con los estudiantes que están listos para aprender.
                        </p>
                    </div>

                </div>

            </article>

        </section>
    );
}

export default About;